<template>
  <div class="canvas-nest"></div>
</template>

<script lang="ts">
import { defineComponent, onMounted, onBeforeUnmount } from 'vue'
import CanvasNest from '../lib/canvas-nest/CanvasNest'

export default defineComponent({
  name: 'CanvasNest',
  props: {
    config: {
      type: Object,
      default: () => {
        return {
          color: '255,0,0',
          count: 99,
          opacity: 0.7,
          zIndex: -1
        }
      }
    },
    el: {
      type: String,
      default: '.vue-canvas-nest-element'
    }
  },
  setup(props) {
    let cn: CanvasNest
    onMounted(() => {
      const elm = document.querySelector<HTMLElement>(props.el)
      if (elm) cn = new CanvasNest(elm, props.config)
    })
    onBeforeUnmount(() => {
      cn.destroy()
    })
  }
})
</script>

<style lang="less" scoped>
.canvas-nest {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  overflow: hidden;
  pointer-events: none;
  z-index: -2;
  opacity: 0.7;
}
</style>
